import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";

import Outings from "./components/Outings";
import Dinings from "./components/Dinings";
import { Routes, Route } from "react-router-dom";
import PlanForm from "./components/PlanForm";

function App() {
  return (
    <div className="App">
      <Routes>
        <Route
          path="/"
          element={
            <div
              className="mt-5"
              style={{ width: "max-content", margin: "auto" }}
            >
              <h1>ALOHA !!!!!</h1>
            </div>
          }
        ></Route>

        <Route path="/dining" element={<Dinings />}></Route>
        <Route path="/outing" element={<Outings />}></Route>
        <Route path="/plan" element={<PlanForm />}></Route>
      </Routes>
    </div>
  );
}

export default App;
