import React, { useState, useEffect } from "react";
import Autocomplete from "@mui/material/Autocomplete";
import TextField from "@mui/material/TextField";
import axios from "axios";
import { Grid } from "@mui/material";
import Button from "@mui/material/Button";
import "./PlanForm.css";

const PlanForm = () => {
  const [selectedComponents, setSelectedComponents] = useState([
    { order: 1, type: "", outing: null, highlight: false },
  ]);

  const handleAddComponent = () => {
    setSelectedComponents((prevComponents) => [
      ...prevComponents,
      {
        order: prevComponents.length + 1,
        type: "",
        outing: null,
        highlight: false,
      },
    ]);
  };

  const handleTypeChange = (index, newValue) => {
    setSelectedComponents((prevComponents) =>
      prevComponents.map((component, i) => {
        if (i === index) {
          // If type is deselected, reset the component selection
          const updatedComponent = {
            ...component,
            type: newValue,
            outing: null,
          };
          if (!newValue) {
            updatedComponent.outing = null;
          }
          return updatedComponent;
        }
        return component;
      })
    );
  };

  const handleOutingChange = (index, newValue) => {
    setSelectedComponents((prevComponents) =>
      prevComponents.map((component, i) =>
        i === index ? { ...component, outing: newValue } : component
      )
    );
  };

  const handleRemoveComponent = (index) => {
    setSelectedComponents((prevComponents) => {
      if (prevComponents.length === 1) {
        console.log("Cannot remove the last component.");
        return prevComponents;
      }

      return prevComponents
        .filter((_, i) => i !== index)
        .map((component, i) => ({ ...component, order: i + 1 }));
    });
  };

  const handleCheckboxChange = (index) => {
    setSelectedComponents((prevComponents) =>
      prevComponents.map((component, i) => {
        if (i === index) {
          return { ...component, highlight: !component.highlight };
        } else {
          return { ...component, highlight: false };
        }
      })
    );
  };

  const [outingOptions, setOutingOptions] = useState([]);
  const [diningOptions, setDiningOptions] = useState([]);

  const [preferredTransport, setPreferredTransport] = useState(null);

  const fetchOutings = async () => {
    const { data } = await axios.get("/api/v1/plan/getOutings");
    setOutingOptions(
      data.outings.map((e) => ({ id: e._id, name: e.place_name }))
    );
    console.log(data.outings.map((e) => ({ id: e._id, name: e.place_name })));
  };

  const fetchDinings = async () => {
    const { data } = await axios.get("/api/v1/plan/getDinings");
    setDiningOptions(
      data.dinings.map((e) => ({ id: e._id, name: e.hotel_name }))
    );
    console.log(data.dinings.map((e) => ({ id: e._id, name: e.hotel_name })));
  };

  const isFormValid = () => {
    const typeError = selectedComponents.some((component) => !component.type);
    const outingError = selectedComponents.some(
      (component) => component.type === "Outing" && !component.outing
    );
    const diningError = selectedComponents.some(
      (component) => component.type === "Dining" && !component.outing
    );

    return (
      selectedComponents.length >= 2 &&
      !typeError &&
      !outingError &&
      !diningError &&
      tile_content.trim() !== "" &&
      selectedTime !== null &&
      preferredTransport !== null
    );
  };

  const [tile_content, setTileContent] = useState("");

  const handleSubmit = async () => {
    if (isFormValid()) {
      const plan_start_time = selectedTime;

      const components = selectedComponents.map((e) => {
        const order = e.order;
        const component_id = e.outing.id;
        const type = e.type;
        const is_highlight = e.highlight;

        return { order, component_id, type, is_highlight };
      });

      try {
        const date = await axios.post("/api/v1/plan/savePlan", {
          plan_start_time,
          components,
          tile_content,
          preferred_transport: preferredTransport,
        });
        console.log(date);
        alert("Response Submitted");
        window.location.reload(false);
      } catch (error) {
        console.log(error);
        alert(`${error.message}`);
      }

      console.log({ components, tile_content, plan_start_time });
    } else {
      console.error("Form validation failed. Please check the input data.");
    }

    // window.location.reload(); //
  };

  const [selectedTime, setSelectedTime] = useState(null);

  const convertTimeToEpochMillis = (time) => {
    if (time) {
      const [hours, minutes] = time.split(":");
      const timeInMilliseconds = new Date();
      timeInMilliseconds.setHours(hours);
      timeInMilliseconds.setMinutes(minutes);
      return timeInMilliseconds.getTime();
    }
    return null;
  };

  useEffect(() => {
    fetchOutings();
    fetchDinings();
  }, []);

  return (
    <div style={{ margin: "32px 80px" }}>
      <div
        style={{ width: "max-content", margin: "auto", marginBottom: "32px" }}
      >
        <h2>Plans Formation</h2>
      </div>

      <div className="my-3">
        <h4>Tile Content</h4>
        <TextField
          label="Tile Content (Max 250 characters)"
          multiline
          rows={4}
          value={tile_content}
          onChange={(e) => setTileContent(e.target.value)}
          inputProps={{ maxLength: 65 }}
          fullWidth
        />
      </div>

      <div className="my-3 mui-time-picker">
        <h4>Select Start Time</h4>
        <input
          type="time"
          onChange={(e) =>
            setSelectedTime(convertTimeToEpochMillis(e.target.value))
          }
        />
      </div>

      <div className="my-3">
        <h4>Preferred Transport</h4>
        <Autocomplete
          options={["Auto", "Scooty", "Bike", "Mid-Size", "SUV"]}
          getOptionLabel={(option) => option}
          value={preferredTransport}
          onChange={(_, newValue) => setPreferredTransport(newValue)}
          renderInput={(params) => (
            <TextField {...params} style={{ width: "100%" }} />
          )}
        />
      </div>

      <div className="my-3">
        <h4>Components of the Plan</h4>
        {selectedComponents.map((component, index) => (
          <Grid container spacing={2} key={index} style={{ margin: "16px 0" }}>
            <Grid item xs={1}>
              <label>
                <input
                  type="checkbox"
                  name="is_highlight"
                  checked={component.highlight}
                  onChange={() => handleCheckboxChange(index)}
                />
                {"  "}Highlight
              </label>
            </Grid>
            <Grid item xs={2}>
              <TextField
                label="Order"
                value={component.order}
                disabled
                style={{ width: "100%" }}
              />
            </Grid>
            <Grid item xs={3}>
              <Autocomplete
                options={["Outing", "Dining"]}
                getOptionLabel={(option) => option}
                value={component.type}
                onChange={(_, newValue) => handleTypeChange(index, newValue)}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    label="Type"
                    style={{ width: "100%" }}
                  />
                )}
              />
            </Grid>
            {component.type === "Outing" && (
              <Grid item xs={4}>
                <Autocomplete
                  options={outingOptions}
                  getOptionLabel={(outing) => outing.name}
                  value={component.outing}
                  onChange={(_, newValue) =>
                    handleOutingChange(index, newValue)
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Outing"
                      style={{ width: "100%" }}
                    />
                  )}
                />
              </Grid>
            )}
            {component.type === "Dining" && (
              <Grid item xs={4}>
                <Autocomplete
                  options={diningOptions}
                  getOptionLabel={(dining) => dining.name}
                  value={component.outing}
                  onChange={(_, newValue) =>
                    handleOutingChange(index, newValue)
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Dining"
                      style={{ width: "100%" }}
                    />
                  )}
                />
              </Grid>
            )}
            <Grid item xs={1}>
              <Button
                variant="contained"
                color="error"
                onClick={() => handleRemoveComponent(index)}
                disabled={selectedComponents.length === 1}
              >
                Remove
              </Button>
            </Grid>
          </Grid>
        ))}

        {selectedComponents.length > 0 && (
          <div style={{ marginLeft: "15px" }}>
            <Button
              variant="contained"
              color="success"
              onClick={handleAddComponent}
            >
              Add Component
            </Button>
          </div>
        )}
      </div>

      <div style={{ width: "max-content", margin: "32px auto" }}>
        <Button
          variant="contained"
          color="primary"
          onClick={handleSubmit}
          disabled={!isFormValid()}
        >
          Submit
        </Button>
      </div>
    </div>
  );
};

export default PlanForm;
